import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import DataTable from '../components/DataTable';
import DateInput from '../components/DateInput';
import getCookie from '../utils/getCookie';

export default class TeacherAttendance extends Component {
  dateRef = React.createRef();
  state = { date: '' };
  dataTable = null;
  constructor(props) {
    super(props);
    const self = this;
    this.dataTable = React.createRef()
    this.dataTableConfig = {
      serverSide: true,
      processing: true,
      ajax: function(params, callback, settings) {
        axios.get('/api/teachers/attendance', {params: {params}}).then(response=>{
          callback(response.data);
        })
      },
      columns: [
        { data: 'firstname', name: 'firstname', 'class': 'text-center'},
        { data: 'lastname', name: 'lastname', 'class': 'text-center'},
        { data: null, 'class': 'text-center', render: function(data,type,row) {
          let checked = data.date ? " checked='' " : ''
          return `<span>
              <div class="bootstrap-switch bootstrap-switch-wrapper bootstrap-switch-animate bootstrap-switch-${data.date ? 'on': 'off'}" style="width: 72px;"><div class="bootstrap-switch-container" style="width: 122px; margin-left: 0px;"><span class="bootstrap-switch-handle-on bootstrap-switch-info" style="width: 50px;"><i class="fa fa-check"></i></span><span class="bootstrap-switch-label" style="width: 30px;">&nbsp;</span><span class="bootstrap-switch-handle-off bootstrap-switch-info" style="width: 50px;"><i class="fa fa-times"></i></span><input type="checkbox" checked="" data-toggle="switch" data-on-color="info" data-off-color="info" data-on-text="<i class='fa fa-check'></i>" data-off-text="<i class='fa fa-times'></i>"></div></div>
            </span>`
        }}
      ],
    };
  }

  dateChanged = (event) => {
    this.setState({date: event.target.value}, this.filterChanged);
  }

  filterChanged = () => {
    this.dataTable.current.draw();
  }

	render() {
		let match = this.props.match;

    let {classes} = this.state;
    let streams = null;

    if(classes) {
      let selectedClass = classes.find( (cls) => cls.id == this.state.class_id );
      if(selectedClass) {
        streams = selectedClass.streams;
      }
    }

		return <div className="container-fluid">
      <DataTable ref={this.dataTable} title="Teachers Attendance" config={this.dataTableConfig} toolbar={
        <>
        <div className="d-flex justify-content-center col-12">
          <div className="col-6">
            <DateInput onChange={this.dateChanged} className="form-control datepicker" placeholder="Enter Date" />
          </div>
        </div>
        </>
      }>
        <th>Firstname</th>
        <th>Lastname</th>
        <th>Attendance</th>
      </DataTable>
		</div>

	}
}
