import React, { Component } from 'react';
import { inject } from 'mobx-react';
import lodash from 'lodash';

import ShowNotification from '../components/ShowNotification';
import DateInput from '../components/DateInput';
import confirmAction from '../utils/confirmAction';

@inject('classesStore')
@inject('subjectStore')
export default class TeacherForm extends Component {
	defaults = {
		firstname: '',
		lastname: '',
		teacher_no: '',
		phone: '',
		email: '',
		id_no: '',
		dob: '',
		date_of_employment: '',
		gender: '',
		subjects: []
	};

	state = {previewUrl: '', error: {}, loading: false, classes: null, subjects: [], loading: false, data: {...this.defaults}
	};

	componentDidMount() {
    this.props.classesStore.list({}, true).then(classes=>{
    	this.setState({classes});
    });

    this.props.subjectStore.list({}, true).then(subjects => {
    	this.setState({subjects})
    })
	}

	componentDidUpdate(prevProps,prevState) {
		if(!lodash.isEqual(this.props.data, prevProps.data)){
			this.setState({data: {...this.state.data,...this.props.data}})
		}
		if((this.props.data||{}).profile != (prevProps.data||{}).profile){
			let url = '/uploads/teachers_profile/' + this.props.data.profile;
			if(this.props.data.profile.startsWith('data:image/')){
				url = this.props.data.profile;
			}
			this.setState({previewUrl: url});
		}

		if(!lodash.isEqual(this.props.error,prevProps.error)){
			this.setState({error: {...this.state.error,...this.props.error}})
		}
	}

	inputChanged = (field,event) => {
		let val = event.target.value;
		this.setState( (state) => state.data[field] = val);
	}

	profileChanged = (event) => {
		let files = event.target.files;
		if(files && files[0]) {
			var reader = new FileReader();
			reader.onload = (e) => {
				this.setState({previewUrl: e.target.result});
				this.setState( (state) => (state.data['profile'] = e.target.result, state));
			};
			reader.readAsDataURL(files[0]);
		}
	}

	removeSubject = (subjectId) => {
		let data = {...this.state.data}
		let subjects = data.subjects || [];
		subjects = [...subjects.filter( id => id != subjectId)];
		data.subjects = subjects;
		this.setState({data});
	}

	addSubject = (subjectId) => {
		let data = {...this.state.data}
		let subjects = data.subjects || [];
		subjects = [...subjects.filter( id => id != subjectId), subjectId];
		console.log('added ' ,subjects);
		data.subjects = subjects;
		this.setState({data});
	}

	saveContinue = () => {
		this.submit().then(()=>{
			this.setState({data: {...this.defaults},error: {}});
		})
	}

	save = () => {
		this.submit().then(()=>this.props.close());
	}

	apply = () => {
		this.submit().then(()=>{
			this.setState({error: {}});
		})
	}

	submit = () => {
		let data = this.state.data;
		this.setState({loading: true});
		return this.props.submit(data).then( res => {
			if(this.props.edit) {
				ShowNotification("Teacher Successfully Edited", "success", "nc-check-2");
			}else {
				ShowNotification("Teacher Created Successfully", "success", "nc-check-2");
			}
		}, error => {
			this.setState({error: error});
			return Promise.reject(error);
		}).finally(_=>this.setState({loading:false}));
	}

	retire = () => {
		confirmAction( () => {
			return this.props.retire().then(res=>{
				this.props.close();
				return {success: 'Retired!', succes_text: 'Teacher was retired'};
			}, error => {
				throw {error: "Teacher couldn't be retired"};
			})
		}, {loading_title: 'Retiring...', loading_text: 'Retiring Teacher', canceled: "Retiring Teacher Operation Cancelled", consequence: 'Teacher information will be archieved'});
	}

	restore = () => {
		confirmAction( () => {
      return this.props.restore().then(res=>{
        this.props.close();
        return {success: 'Restored!', succes_text: 'Teacher was restored'};
      }, error => {
        throw {error: 'Teacher could not be restored.'};
      })
    }, {loading_title: 'Restoring...', loading_text: 'Restoring Teacher', canceled: "Restoring Teacher Operation Cancelled", consequence: 'Teacher information will be archieved'});
	}

	delete = () => {
		confirmAction( () => {
			return this.props.delete().then(res=>{
				this.props.close();
				return {success: 'Deleted!', succes_text: 'Teacher was deleted'};
			}, error => {
				throw {error: "Teacher couldn't be deleted."};
			})
		}, {loading_title: 'Deleting...', loading_text: 'Deleting Teacher', canceled: "Deleting Teacher Operation Cancelled", consequence: 'Teacher will be completely removed'});
	}

	cancel = () => {
		let cancelAsync = async () => this.props.cancel();
		if(lodash.isEmpty(this.props.data) || (!this.props.data.__meta_pending && lodash.isEqual(this.props.data, this.state.data))) {
			this.props.close();
			return;
		}
		confirmAction( () => {
			return cancelAsync().then(res=>{
				this.props.close();
				return {success: 'Cancelled!', succes_text: 'Teacher entry was cancelled'};
			}, error => {
				throw {error: "Teacher entry couldn't be removed."};
			})
		}, {loading_title: 'Deleting...', loading_text: 'Cancelling entry', canceled: "", consequence: 'Entered information will be discarded'});
	}

	handleSubmit = (e) => {
		e.preventDefault();
		this.submitAction();
	}

	chooseAction = (e) => {
		switch(e.target.name) {
			case 'apply':
				this.submitAction = this.apply;
				break;
			case 'save':
				this.submitAction = this.save;
				break;
			case 'save_continue':
				this.submitAction = this.saveContinue;
				break;
		}
	}

	subjectName = (subjectId) => {
		let subject = this.state.subjects.find( subject => subject.id == subjectId);
		if(subject)
			return subject.shorthand.toUpperCase();
		return 'SUB';
	}

	render() {
		let { classes, subjects, data } = this.state;

		let error = this.state.error || {};
		let firstnameInvalid='', lastnameInvalid='', teacherNoInvalid='', phoneInvalid='', emailInvalid='', idNoInvalid='', genderInvalid='', dobInvalid='', dateOfEmploymentInvalid='';
		if(error.errors) {
			firstnameInvalid = error.errors.firstname ? 'is-invalid' : '';
			lastnameInvalid = error.errors.lastname ? 'is-invalid' : '';
			teacherNoInvalid = error.errors.teacher_no ? 'is-invalid' : '';
			phoneInvalid = error.errors.phone ? 'is-invalid' : '';
			emailInvalid = error.errors.email ? 'is-invalid' : '';
			idNoInvalid = error.errors.id_no ? 'is-invalid' : '';
			genderInvalid = error.errors.gender ? 'is-invalid' : '';
			dobInvalid = error.errors.dob ? 'is-invalid' : '';
			dateOfEmploymentInvalid = error.errors.date_of_employment ? 'is-invalid' : '';
		}

		let streams = null;

		if(classes) {
			let selectedClass = classes.find( (cls) => cls.id == this.state.data.class_id );
			if(selectedClass) {
				streams = selectedClass.streams;
			}
		}

		let teachingSubjects = data.subjects || [];

		return <div className="container-fluid">
			{this.props.edit && <div className="row">
    	    <div className="col-md-12">
    	      <span className="pull-right">
    	      	{this.state.data.date_left ? <button type="button" className="btn btn-secondary" onClick={this.restore} >Restore</button>
    	      	 : <button type="button" className="btn btn-warning" onClick={this.retire} >Retire</button> }
    	      	&nbsp;
    	        <button type="button" className="btn btn-danger" onClick={this.delete} >Delete</button>
    	      </span>
    	    </div>
	    </div>}
		  <div className="row">
		    <div className="col-md-12">
		      <div className="card stacked-form">
					  <div className="card-header ">
					    <h4 className="card-title">Teacher Details</h4>
					  </div>
					  <div className="card-body ">
					    <form method="#" action="#" onSubmit={this.handleSubmit}>
					    	{error.message && <div className="alert alert-danger">
                    <span>
                        <b> Error - </b> {error.message}
                    </span>
                </div>}
					    	<div className="row">
					    		<div className="col-md-9">
					    				<div className="form-group">
								        <label>First Name</label>
								        <input value={this.state.data.firstname} onChange={this.inputChanged.bind(this,'firstname')} type="text" placeholder="First Name" className={`form-control ${firstnameInvalid}`} required />
								        {firstnameInvalid && error.errors.firstname.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
												<p className="help-block help-block-error"></p>
								      </div>

								      <div className="form-group">
								        <label>Last Name</label>
								        <input value={this.state.data.lastname} onChange={this.inputChanged.bind(this,'lastname')} type="text" placeholder="Last Name" className={`form-control ${lastnameInvalid}`} required />
								        {lastnameInvalid && error.errors.lastname.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
												<p className="help-block help-block-error"></p>
								      </div>

								      <div className="form-group">
								        <label>Gender</label>
								        <select value={this.state.data.gender} onChange={this.inputChanged.bind(this,'gender')} name="gender" className={`form-control ${genderInvalid}`} required >
								        	<option value="">Select Gender</option>
		                      <option value="M">Male</option>
		                      <option value="F">Female</option>
		                    </select>
								        {genderInvalid && error.errors.gender.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
												<p className="help-block help-block-error"></p>
								      </div>
					    		</div>
						      <div className="col-md-3 d-flex align-items-center">
						      	<div>
						      	
						      	<div className="input-group">
						      		<div>
							      		<img src={this.state.previewUrl || '/img/profile-placeholder.jpg'} className="rounded img-fluid" style={{width: '100%', height: '100%'}} alt="Responsive image" />
							      	</div>
										  <span style={{position: 'absolute', bottom: 0}}>
										  	<span className="btn btn-primary"><i className="fa fa-camera"></i>Change</span>
										    <input type="file" onChange={this.profileChanged} style={{position: 'absolute', bottom: 0, top: 0, right: 0, left: 0}} className="custom-file-input" id="inputGroupFile01" aria-describedby="inputGroupFileAddon01" />
										  </span>
										</div>
										</div>
						      </div>
					      </div>

					      <div className="row">
					      	<div className="form-group col-md-6">
						        <label>Teacher's No</label>
						        <input value={this.state.data.teacher_no} onChange={this.inputChanged.bind(this,'teacher_no')} type="text" placeholder="Teacher No" className={`form-control ${teacherNoInvalid}`} />
						        {teacherNoInvalid && error.errors.teacher_no.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
					      	
					      	<div className="form-group col-md-6">
						        <label>Phone Number</label>
						        <input value={this.state.data.phone} onChange={this.inputChanged.bind(this,'phone')} type="text" placeholder="Phone Number" className={`form-control ${phoneInvalid}`} />
						        {phoneInvalid && error.errors.phone.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						    </div>

						    <div className="row">
					      	<div className="form-group col-md-6">
						        <label>Email</label>
						        <input value={this.state.data.email} onChange={this.inputChanged.bind(this,'email')} type="text" placeholder="Email" className={`form-control ${emailInvalid}`} />
						        {emailInvalid && error.errors.email.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
					      	
					      	<div className="form-group col-md-6">
						        <label>ID Number</label>
						        <input value={this.state.data.id_no} onChange={this.inputChanged.bind(this,'id_no')} type="text" placeholder="ID Number" className={`form-control ${idNoInvalid}`} />
						        {idNoInvalid && error.errors.id_no.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						    </div>

					      <div className="row">
						      <div className="form-group col-md-6">
						        <label>Date of Birth</label>
						        <DateInput value={this.state.data.dob} onChange={this.inputChanged.bind(this,'dob')} type="text" placeholder="Date of Birth" className={`form-control ${dobInvalid}`} />
						        {dobInvalid && error.errors.dob.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						      <div className="form-group col-md-6">
						        <label>Date of Employment</label>
						        <DateInput value={this.state.data.date_of_employment} onChange={this.inputChanged.bind(this,'date_of_employment')} type="text" placeholder="Date of Employment" className={`form-control ${dateOfEmploymentInvalid}`} />
						        {dateOfEmploymentInvalid && error.errors.date_of_employment.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						    </div>

						    <div className="row">
						    	<div className="form-group d-flex align-items-center">
						    		<label className="mr-2">Subjects: </label>
						    		{teachingSubjects.map( subjectId => <h3 key={subjectId} className="mr-2">
						    			<span className="badge badge-pill badge-info d-flex align-items-center">
						    				{this.subjectName(subjectId)} <span onClick={this.removeSubject.bind(this,subjectId)} className="nc-icon nc-simple-remove bg-success rounded-circle ml-1"></span>
						    			</span>
						    		</h3>) }
						    		<div className="dropdown">
										  <button className="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
										    Add Subject
										  </button>
										  <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
										  	{subjects.map( subject => <a key={subject.id} className="dropdown-item" onClick={this.addSubject.bind(this,subject.id)}>{subject.name}</a> )}
										  </div>
										</div>
						    	</div>
						    </div>

						    <div className="row">
						    	<div className="col-sm-2">
				            <input onClick={this.chooseAction} type="submit" name="save" value="SAVE" disabled={this.state.loading} className="btn btn-success" />
				          </div>
							  	{this.props.edit ? <div className="col-sm-2">
				            	<input onClick={this.chooseAction} type="submit" name="apply" value="APPLY" disabled={this.state.loading} className="btn btn-primary" />
				          	</div> :
				          	<div className="col-sm-3">
				            	<input onClick={this.chooseAction} type="submit" name="save_continue" value="SAVE AND CONTINUE" disabled={this.state.loading} className="btn btn-primary" />
				          	</div>
				          }
				          <div className="col-sm-3">
				            	<input onClick={this.cancel} type="button" value="CANCEL" disabled={this.state.loading} className="btn btn-danger" />
				          </div>
						    </div>

					    </form>
					  </div>
					  <div className="card-footer ">
					  </div>


					  


					</div>
		    </div>
		  </div>
		</div>

	}
}