import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { inject } from 'mobx-react';
import lodash from 'lodash';
import $ from 'jquery';

import TodForm from '../components/TodForm';
import DataTable from '../components/DataTable';
import getCookie from '../utils/getCookie';

@inject('teacherStore')
@inject('teacherDutyStore')
export default class Tod extends Component {
    constructor(props) {
        super(props);
        window.todListActionEdit = this.edit;
        const self = this;
        this.state = {teachers: []}
        this.dataTable = React.createRef();
        this.dataTableConfig = {
            serverSide: false,
            processing: false,
            ajax : function (params, callback, settings) {
                self.props.teacherDutyStore.list(params,true).then(tods=>{
                    callback({data: tods});
                })
            },
            columns: [
                { data: 'start', name: 'start', 'class': 'text-center', render: (data,type,row) => (new Date(data)).toDateString()},
                { data: 'end', name: 'end', 'class': 'text-center', render: (data,type,row) => (new Date(data)).toDateString()},
                { data: 'firstname', name: 'firstname', 'class': 'text-center', render: (data,type,row) => data || ''},
                { data: 'lastname', name: 'lastname', 'class': 'text-center', render: (data,type,row) => data || ''},
                { data: null, 'class': 'text-center', render: function(data,type,row) {
                    return `<span>
                      <a onclick="todListActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
                  </span>`
                }}
            ],
            rowCallback: function( row, data ) {
                if(!data.firstname || !data.lastname && self.state.teachers) {
                    let teacher = self.state.teachers.find( t => t.id == data.teacher_id);
                    if(teacher) {
                        data.firstname = teacher.firstname;
                        data.lastname = teacher.lastname;
                        self.dataTable.current.table.row(row).data(data);
                    }
                }
            }
        };
    }

    componentDidMount() {
        this.props.teacherStore.list({},true).then(teachers => {
            this.setState({teachers}, ()=>{
                this.dataTable.current.table.draw();
            });
        })
    }

    componentWillUnmount() {
        $('tr[title]').tooltip('dispose');
    }

    edit = (duty) => {
        if(String(duty).startsWith('dummy_')){
            this.props.history.push(`${this.props.match.url}/new?teacher_duty=${duty}`)
        } else {
            this.props.history.push(`${this.props.match.url}/${duty}`);
        }
    }

	render() {
		let match = this.props.match;
		return <div className="container-fluid">
    	    <div className="row">
        	    <div className="col-md-12">
        	      <span className="pull-right">
        	        <Link to={`${match.url}/new`} className="btn btn-success details">Add New Duty Slot</Link>
        	      </span>
        	    </div>
    	    </div>
            <DataTable ref={this.dataTable} title="Teachers on Duty" config={this.dataTableConfig}>
                <th>Start</th>
                <th>End</th>
                <th>Firstname</th>
                <th>Lastname</th>
                <th className="disabled-sorting text-center noExport">Actions</th>
            </DataTable>
		</div>

	}
}

@inject('teacherDutyStore')
export class NewTod extends Component {
    state = { error: {} };

    componentDidMount() {
        let teacherDutyIdMatch = this.props.location.search.match(/teacher_duty=([^&]+)/);
        if(teacherDutyIdMatch) {
            let teacherDutyId = teacherDutyIdMatch[1];
            this.props.teacherDutyStore.get(teacherDutyId, true).then(tod => {
                if(tod){
                    this.setState({data: tod, error: tod.error});
                }
            })
        }
    }

    submit = (data) => {
        return this.props.teacherDutyStore.create(data).then( tod => {
            this.setState({error: {}, data: tod})
            return tod;
        }, error => {
            this.setState({error});
            return Promise.reject(error);
        })
    }

    cancel = () => {
        let teacherDutyIdMatch = this.props.location.search.match(/teacher_duty=([^&]+)/);
        if(teacherDutyIdMatch) {
            let teacherDutyId = teacherDutyIdMatch[1];
            return this.props.teacherDutyStore.cancel(teacherDutyId);
        }
    }

    close = () => {
        this.props.history.push('/school/teachers/tod');
    }
    render() {
        return <TodForm data={this.state.data} error={this.state.error} submit={this.submit} cancel={this.cancel} close={this.close} />
    }
}

@inject('teacherDutyStore')
export class EditTod extends Component {
    state = { error: {}, data: {} };

    componentDidMount() {
    if(this.props.location.state && this.props.location.state.tod){
        this.setState({data: this.props.location.state.tod})
    }else if(this.props.match.params.id){
        this.props.teacherDutyStore.get(this.props.match.params.id).then(tod => {
            this.setState({data: tod, error: (tod.error || {})});
        },error=>this.setState({error}))
    }
    
  }

    submit = (data) => {
        return this.props.teacherDutyStore.update(data).then( tod => {
            this.setState({error: {}, data: tod})
            return tod;
        }, error => {
            this.setState({error});
            return Promise.reject(error);
        })
    }

    delete = () => {
        return this.props.teacherDutyStore.delete(this.props.match.params.id);
    }

    cancel = () => {
        return this.props.teacherDutyStore.cancel(this.props.match.params.id);
    }

    close = () => {
        this.props.history.push('/school/teachers/tod');
    }

    render() {
        return <TodForm edit={true} error={this.state.error} data={this.state.data} submit={this.submit} delete={this.delete} cancel={this.cancel} close={this.close} />
    }
}