import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { inject } from 'mobx-react';
import $ from 'jquery';

import TeacherAssessmentForm from '../components/TeacherAssessmentForm';
import DataTable from '../components/DataTable';
import Tabledit from '../components/Tabledit';
import getCookie from '../utils/getCookie';

@inject('teacherStore')
export default class TeachersAssessmentList extends Component {
    constructor(props) {
        super(props);
        window.teacherAssessmentActionList = this.showAssessment;
        const self = this;
        this.dataTable = React.createRef();
        this.dataTableConfig = {
            serverSide: false,
            processing: false,
            ajax : function (params, callback, settings) {
                self.props.teacherStore.list(params,true).then(teachers=>{
                    callback({data: teachers});
                })
            },
            columns: [
                { data: 'teacher_no', name: 'teacher_no', 'className': 'text-center'},
                { data: 'firstname', name: 'firstname', 'className': 'text-center'},
                { data: 'lastname', name: 'lastname', 'className': 'text-center'},
                { data: null, 'className': 'text-center', render: function(data,type,row) {
                    return `<span>
                      <a onclick="teacherAssessmentActionList('${data.id}')" class="btn btn-link btn-warning details"><i class="nc-icon nc-bullet-list-67 icon-bold"></i></a>
                  </span>`
                }}
            ],
            createdRow: function (row, data, index) {
                if(data.date_left) {
                    row.classList.add('table-danger');
                }
            }
        };
    }

    showAssessment = (teacher) => {
        this.props.history.push(`${this.props.match.url}/${teacher}`);
    }

	render() {
		let match = this.props.match;
		return <div className="container-fluid">
            <DataTable ref={this.dataTable} title="Teachers Assessment" config={this.dataTableConfig}>
                <th>Teacher's No</th>
                <th>Firstname</th>
                <th>Lastname</th>
                <th className="disabled-sorting text-center noExport">Actions</th>
            </DataTable>
		</div>

	}
}

@inject('teacherAssessmentStore')
@inject('teacherStore')
export class TeacherAssessmentList extends Component {
    constructor(props) {
        super(props);
        window.teacherAssessmentActionEdit = this.edit;
        const self = this;
        this.state = {teachers: []};
        this.dataTable = React.createRef();
        this.dataTableConfig = {
            serverSide: false,
            processing: false,
            ajax : function (params, callback, settings) {
                params['teacher_id'] = self.props.match.params.teacherId
                self.props.teacherAssessmentStore.list(params,true, true).then(assessments=>{
                    callback({data: assessments});
                })
            },
            columns: [
                { data: 'teacher_no', name: 'teacher_no', 'className': 'text-center', render: (data,type,row) => data || ''},
                { data: 'tsc', name: 'tsc', 'className': 'text-center'},
                { data: 'firstname', name: 'firstname', 'className': 'text-center', render: (data,type,row) => data || ''},
                { data: 'lastname', name: 'lastname', 'className': 'text-center', render: (data,type,row) => data || ''},
                { data: 'from', name: 'from', 'className': 'text-center', render: (data,type,row) => (new Date(data)).toDateString() },
                { data: 'to', name: 'to', 'className': 'text-center', render: (data,type,row) => (new Date(data)).toDateString() },
                { data: null, 'className': 'text-center', render: function(data,type,row) {
                    return `<span>
                      <a onclick="teacherAssessmentActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
                  </span>`
                }}
            ],
            rowCallback: function( row, data ) {
                if(!data.firstname && self.state.teachers) {
                    let teacher = self.state.teachers.find( t => t.id == data.teacher_id);
                    if(teacher) {
                        data.firstname = teacher.firstname;
                        data.lastname = teacher.lastname;
                        data.teacher_no = teacher.teacher_no;
                        self.dataTable.current.table.row(row).data(data);
                    }
                }
            }
        };
    }

    componentDidMount() {
        this.props.teacherStore.list({},true).then(teachers => {
            this.setState({teachers}, ()=>{
                this.dataTable.current.table.draw();
            });
        })
    }

    componentWillUnmount() {
        $('tr[title]').tooltip('dispose');
    }

    edit = (assessment) => {
        if(String(assessment).startsWith('dummy_')){
            this.props.history.push(`${this.props.match.url}/new?teacher_assessment=${assessment}`)
        } else {
            this.props.history.push(`${this.props.match.url}/${assessment}`);
        }
    }

    render() {
        let match = this.props.match;
        return <div className="container-fluid">
            <div className="row">
                <div className="col-md-12">
                  <span className="pull-right">
                    <Link to={`${match.url}/new`} className="btn btn-success details">New Assessment</Link>
                  </span>
                </div>
            </div>
            <DataTable ref={this.dataTable} title="Teacher Assessment" config={this.dataTableConfig}>
                <th>Teacher's No</th>
                <th>TSC NO</th>
                <th>Firstname</th>
                <th>Lastname</th>
                <th>From</th>
                <th>To</th>
                <th className="disabled-sorting text-center noExport">Actions</th>
            </DataTable>
        </div>

    }
}

@inject('teacherAssessmentStore')
export class TeacherAssessmentNew extends Component {
    state = { error: {}, data: null };

    componentDidMount() {
        let teacherAssessmentIdMatch = this.props.location.search.match(/teacher_assessment=([^&]+)/);
        if(teacherAssessmentIdMatch) {
            let teacherAssessmentId = teacherAssessmentIdMatch[1];
            this.props.teacherAssessmentStore.get(teacherAssessmentId, true).then(assessment => {
                if(assessment){
                    this.setState({data: assessment, error: assessment.error});
                }
            })
        }
    }

    submit = (data) => {
        data.teacher_id = parseInt(this.props.match.params.teacherId) || this.props.match.params.teacherId;
        return this.props.teacherAssessmentStore.create(data).then( assessment => {
            this.setState({error: {}, data: assessment})
            return assessment;
        }, error => {
            this.setState({error});
            return Promise.reject(error);
        })
    }

    cancel = () => {
        let teacherAssessmentIdMatch = this.props.location.search.match(/teacher_assessment=([^&]+)/);
        if(teacherAssessmentIdMatch) {
            let teacherAssessmentId = teacherAssessmentIdMatch[1];
            return this.props.teacherAssessmentStore.cancel(teacherAssessmentId);
        }
    }

    close = () => {
        this.props.history.push(`/school/teachers/assessment/${this.props.match.params.teacherId}`);
    }
    render() {
        return <TeacherAssessmentForm data={this.state.data} error={this.state.error} submit={this.submit} cancel={this.cancel} close={this.close} />
    }
}

@inject('teacherAssessmentStore')
export class TeacherAssessmentEdit extends Component {
    state = { error: {}, data: {} };

    componentDidMount() {
        if(this.props.location.state && this.props.location.state.assessment){
            this.setState({data: this.props.location.state.assessment})
        }else if(this.props.match.params.assessmentId){
            this.props.teacherAssessmentStore.get(this.props.match.params.assessmentId).then(assessment => {
                this.setState({data: assessment, error: (assessment.error||{})});
            },error=>this.setState({error}))
        }
    }

    submit = (data) => {
        return this.props.teacherAssessmentStore.update(data).then( assessment => {
            this.setState({error: {}, data: assessment})
            return assessment;
        }, error => {
            this.setState({error});
            return Promise.reject(error);
        })
    }

    delete = () => {
        return this.props.teacherAssessmentStore.delete(this.props.match.params.assessmentId);
    }

    cancel = () => {
        return this.props.teacherAssessmentStore.cancel(this.props.match.params.assessmentId);
    }

    close = () => {
        this.props.history.push(`/school/teachers/assessment/${this.props.match.params.teacherId}`);
    }

    render() {
        return <TeacherAssessmentForm edit={true} error={this.state.error} data={this.state.data} submit={this.submit} delete={this.delete} cancel={this.cancel} close={this.close} />
    }
}