import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Link } from 'react-router-dom';
import lodash from 'lodash';
import $ from 'jquery';

import TeacherForm from '../components/TeacherForm';
import DataTable from '../components/DataTable';
import getCookie from '../utils/getCookie';

@inject('teacherStore')
@observer
export default class Teacher extends Component {
    constructor(props) {
        super(props);
        window.teacherListActionEdit = this.edit;
        const self = this;
        this.dataTable = React.createRef();
        this.dataTableConfig = {
            serverSide: false,
            processing: false,
            ajax : function (params, callback, settings) {
                self.props.teacherStore.list(params,true, true).then(teachers=>{
                    callback({data: teachers});
                })
            },
            columns: [
                { data: 'teacher_no', name: 'teacher_no', 'class': 'text-center'},
                { data: 'firstname', name: 'firstname', 'class': 'text-center'},
                { data: 'lastname', name: 'lastname', 'class': 'text-center'},
                { data: 'phone', name: 'phone', 'class': 'text-center'},
                { data: 'email', name: 'email', 'class': 'text-center'},
                { data: 'gender', name: 'gender', 'class': 'text-center'},
                { data: null, 'class': 'text-center', render: function(data,type,row) {
                    return `<span>
                      <a onclick="teacherListActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
                  </span>`
                }}
            ]
        };
    }

    componentWillUnmount() {
        $('tr[title]').tooltip('dispose');
    }

    edit = (teacher) => {
        if(String(teacher).startsWith('dummy_')){
            this.props.history.push(`${this.props.match.url}/new?teacher=${teacher}`)
        } else {
            this.props.history.push(`${this.props.match.url}/${teacher}`);
        }
    }

	render() {
		let match = this.props.match;
		return <div className="container-fluid">
    	    <div className="row">
        	    <div className="col-md-12">
        	      <span className="pull-right">
        	        <Link to={`${match.url}/new`} className="btn btn-success details">New Teacher</Link>
        	      </span>
        	    </div>
    	    </div>
            <DataTable ref={this.dataTable} title="Teachers" config={this.dataTableConfig} softDeleteField="date_left">
                <th>Teacher's No</th>
                <th>Firstname</th>
                <th>Lastname</th>
                <th>Phone Number</th>
                <th>Email</th>
                <th>Gender</th>
                <th className="disabled-sorting text-center noExport">Actions</th>
            </DataTable>
		</div>

	}
}

@inject('teacherStore')
export class NewTeacher extends Component {
    state = { error: {} };

    componentDidMount() {
        let teacherIdMatch = this.props.location.search.match(/teacher=([^&]+)/);
        if(teacherIdMatch) {
            let teacherId = teacherIdMatch[1];
            this.props.teacherStore.get(teacherId, true).then(teacher => {
                if(teacher){
                    this.setState({data: teacher, error: teacher.error});
                }
            })
        }
    }

    submit = (data) => {
        return this.props.teacherStore.create(data).then( teacher => {
            this.setState({error: {}, data: teacher})
            return teacher;
        }, error => {
            this.setState({error});
            return Promise.reject(error);
        })
    }

    cancel = () => {
        let teacherIdMatch = this.props.location.search.match(/teacher=([^&]+)/);
        if(teacherIdMatch) {
            let teacherId = teacherIdMatch[1];
            return this.props.teacherStore.cancel(teacherId);
        }
    }

    close = () => {
        this.props.history.push('/school/teachers');
    }
    render() {
        return <TeacherForm data={this.state.data} error={this.state.error} submit={this.submit} cancel={this.cancel} close={this.close} />
    }
}

@inject('teacherStore')
export class EditTeacher extends Component {
    state = { error: {}, data: {} };

    componentDidMount() {
        if(this.props.location.state && this.props.location.state.teacher){
            this.setState({data: this.props.location.state.teacher})
        }else if(this.props.match.params.id){
            this.props.teacherStore.get(this.props.match.params.id).then(teacher => {
                this.setState({data: teacher,  error: (teacher.error || {}) });
            },error=>this.setState({error}))
        }
    }

    submit = (data) => {
        return this.props.teacherStore.update(data).then( teacher => {
            this.setState({error: {}, data: teacher})
            return teacher;
        }, error => {
            this.setState({error});
            return Promise.reject(error);
        })
    }

    delete = () => {
        return this.props.teacherStore.delete(this.props.match.params.id);
    }

    retire = () => {
        return axios.post(`/api/teachers/retire/${this.state.data.id}`).then(res=> {
            this.props.teacherStore.updateItem(res.data);
            this.setState({error: {}, data: res.data});
            return res.data;
        }, error => {
            error = error.response ? error.response.data : error;
            this.setState({error});
            throw error;
        })
    }

    restore = () => {
        return axios.post(`/api/teachers/restore/${this.state.data.id}`).then(res=> {
            this.props.teacherStore.updateItem(res.data);
            this.setState({error: {}, data: res.data});
            return res.data;
        }, error => {
            error = error.response ? error.response.data : error;
            this.setState({error});
            throw error;
        })
    }

    cancel = () => {
        return this.props.teacherStore.cancel(this.props.match.params.id);
    }

    close = () => {
        this.props.history.push('/school/teachers');
    }

    render() {
        return <TeacherForm edit={true} error={this.state.error} data={this.state.data} submit={this.submit} cancel={this.cancel} delete={this.delete} retire={this.retire} restore={this.restore} close={this.close} />
    }
}