import React, { Component } from 'react';
import { Route } from "react-router-dom";

import AnimatedSwitch from '../components/animated-switch';

import Teacher, {NewTeacher, EditTeacher} from './Teacher';
import Tod, {NewTod, EditTod} from './Tod';
import TeacherAttendance from './TeacherAttendance';
import TeachersAssessmentList, {TeacherAssessmentList, TeacherAssessmentNew, TeacherAssessmentEdit} from './TeacherAssessment';

export default class TeacherPages extends Component {
	render() {
		let match = this.props.match;
		return <AnimatedSwitch>
						<Route exact path={`${match.path}`} render={(props) => <Teacher {...props} /> } />
						<Route path={`${match.path}/new`} render={(props) => <NewTeacher {...props} />} />
						<Route path={`${match.path}/attendance`} render={(props) => <TeacherAttendance {...props} />} />
						<Route path={`${match.path}/tod/new`} render={(props) => <NewTod {...props} />} />
						<Route path={`${match.path}/tod/:id`} render={(props) => <EditTod {...props} />} />
						<Route path={`${match.path}/tod`} render={(props) => <Tod {...props} />} />
						<Route path={`${match.path}/assessment/:teacherId/new`} render={(props) => <TeacherAssessmentNew {...props} />} />
						<Route path={`${match.path}/assessment/:teacherId/:assessmentId`} render={(props) => <TeacherAssessmentEdit {...props} />} />
						<Route path={`${match.path}/assessment/:teacherId`} render={(props) => <TeacherAssessmentList {...props} />} />
						<Route path={`${match.path}/assessment`} render={(props) => <TeachersAssessmentList {...props} />} />
						<Route exact path={`${match.path}/:id`} render={(props) => <EditTeacher {...props} />} />
						<Route path=""><h1>404 Not Found</h1></Route>
					</AnimatedSwitch>
	}
}